figcaption {
  position: absolute;
  text-align: center;
  color: rgb(0, 0, 0);
  font-size: 3rem;
  font-family: 'Courgette',
    cursive;
  width: 100%;
}

.imgHome {
  width: 100vw;
  height: 550px;
  opacity: 0.7;
  object-fit: cover;
  object-position: 20% 35%;
  z-index: -1;
}

@media screen and (max-width: 760px) {
  figcaption {
    position: absolute;
    width: 100%;
    font-size: 1.4rem;
    color: rgb(0, 0, 0);
  }
}

@media screen and (max-width: 570px) {
  .imgHome {
    object-position: 50% 20%;
    height: 30vh;
  }
}